import { getProductListOne, getCategoryList } from '@/api/goods.js'
import XTabbar from '@/components/tabbar.vue'
import orderClassify from '@/components/orderClassify.vue'
import nodata from '@/components/noData'

export default {
  name: 'homeIndex',
  components: {
    XTabbar,
    nodata,
    orderClassify
  },
  created () {
    // 获取分类
    getCategoryList()
      .then(res => {
        if (res.data.errorcode === 0) {
          // 处理数据
          console.log(res)
          // 因为分类结构问题 暂时默认所有分类下的
          this.cateList = res.data.data[0].childlist
          // this.cateList = this.cateList.concat(this.cateList)
          // 获取路由参数
          if (this.$route.query.cateid) {
            // 存在分类id  读取对应分类
            console.log(this.$route.query.cateid)
            this.cateList.map(item => {
              if(Number(item.cid) === Number(this.$route.query.cateid)) {
                this.curCate = item.catename
                this.postData.cid = item.cid
                // 读取列表
                this.handleGetList()
                return
              }
            })
          }
          else
          {
            // 读取列表
            this.handleGetList()
          }
        } else {
          this.$vux.toast.text(res.data.errormsg, 'middle')
        }
      })
      .catch(err => {
        console.log(err)
        this.$vux.toast.text('网络异常！', 'middle')
      })
  },
  data () {
    return {
      noDataShow: false,
      visible: false,
      top: 0,
      left: 0,
      selectedTag: {},
      date: '2018-08-17',
      loadingStatus: true,
      pageStatus: false,
      show: false,
      skulist: [],
      filterShow: '',
      cateList: [],
      curCate: '全部商品',
      tagsList: [
        {
          name: 'Hello, World'
        }
      ],
      postData: {
        cid: 1,
        sortorder: '',
        page: 1,
        pagenum: 20
      }
    }
  },
  filters: {
    cateFilter (val) {
    }
  },
  methods: {
    // 获取列表
    handleGetList () {
      if (this.$route.path != "/classify") {
        return
      }
      if (this.pageStatus) {
        return
      }
      this.$vux.loading.show()
      this.noDataShow = false
      getProductListOne(this.postData)
        .then(res => {
          this.$vux.loading.hide()
          this.noDataShow = true
          if (res.data.errorcode === 0) {
            // 处理数据
            this.skulist = this.skulist.concat(res.data.data.skulist)
            if (this.skulist.length == res.data.totalpage || this.skulist.length > res.data.totalpage) {
              this.pageStatus = true
            } else {
              this.postData.page++
            }
          } else {
            this.$vux.toast.text(res.data.errormsg, 'middle')
            console.log(res)
          }
        })
        .catch(err => {
          this.$vux.loading.hide()
          this.noDataShow = true
          this.$vux.toast.text('网络异常！', 'middle')
          console.log(err)
        })
    },
    // 分类点击
    handleCate (item) {
      this.curCate = item.catename
      // this.$set(this.postData, 'cid', item.cid)
      this.postData.cid = item.cid
      console.log(this.postData.cid)
      this.pageStatus = false
      this.postData.page = 1
      this.skulist = []
      this.handleGetList()
    },
    // 排序
    handleSort (item) {
      if (item === 'price') {
        // 价格排序  pricedesc 高-》低
        if (this.postData.sortorder === 'saledesc' || this.postData.sortorder === 'saleasc' || this.postData.sortorder === '') {
          // 默认低到高
          console.log(1212)
          this.postData.sortorder = 'priceasc'
        } else {
          this.postData.sortorder = this.postData.sortorder === 'pricedesc' ? 'priceasc' : 'pricedesc'
        }
      } else if (item === 'sale') {
        // 销量排序
        if (this.postData.sortorder === 'pricedesc' || this.postData.sortorder === 'priceasc' | this.postData.sortorder === '') {
          // 默认低到高
          this.postData.sortorder = 'saleasc'
        } else {
          this.postData.sortorder = this.postData.sortorder === 'saledesc' ? 'saleasc' : 'saledesc'
        }
      }
      // 请求接口
      console.log(this.postData)
      this.pageStatus = false
      this.skulist = []
      this.postData.page = 1
      this.handleGetList()
    },
    touchmove(e){
      //变量scrollTop是滚动条滚动时，距离顶部的距离
      var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
      //变量wndowHeight是可视区的高度
      var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
      //变量scrollHeight是滚动条的总高度
      var scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight;
            //滚动条到底部的条件
      // console.log(163, scrollTop)
        if(parseInt(scrollHeight)-parseInt(scrollTop+windowHeight)< 80){
        //写后台加载数据的函数
        if (this.loadingStatus) {
          setTimeout(()=>{
            this.handleGetList()
          },100)
          this.loadingStatus = false
          setTimeout(()=>{
            this.loadingStatus = true
          },1000)
        }
      }
    },
  },
  mounted() {
    window.addEventListener('scroll', this.touchmove)
  }
}



